@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
body {
  box-sizing: border-box;
  --blue-color: #00acdc;
  width: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.logo {
  box-sizing: border-box;
  margin-left: 50px;
  display: flex;
}

nav {
  box-sizing: border-box;
  margin-right: 50px;
}

.nav {
  box-sizing: border-box;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link {
  box-sizing: border-box;
  color: #000;
  text-transform: uppercase;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: var(--blue-color);
  cursor: pointer;
}

.nav .navlink:last-child .link {
  margin-right: 0;
}

.is-active {
  color: var(--blue-color);
  height: 60px;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

